<template>
  <div class="form" id="form">
    <div class="container">
      <div class="row">
        <div class="text-center text-md-left col-lg-6 mb-5 mb-lg-0 d-flex justify-content-center align-items-center">
          <div>
            <h2 class="mb-4">Оствьте заявку</h2>
            <h5>Если у вас есть вопросы или вы хотите обсудить ваш заказ, оставьте <br> нам ваш номер телефона или Email и мы
              <br> свяжемся с вами.</h5>
