        </div>
        <div class="col-lg-6 d-flex justify-content-center align-items-center">
          <div>
            <form action="https://formspree.io/rus131354@icloud.com" method="post">
              <div class="d-none d-md-block">
                <label for="pole">Номер телефона или Email</label>
                <div class="input-group mb-3">
                  <input id="pole" name="message" type="text" class="form-control" placeholder="+7 123 456 78 90" aria-describedby="basic-addon2" required>
                  <div class="input-group-append pl-1">
                    <button>Оставить заявку</button>
                  </div>
                </div>
              </div>
              <div class="d-block d-md-none">
                <label for="pole">Номер телефона или Email</label>
                <input id="pole" name="message" type="text" class="form-control rounded-0" placeholder="+7 123 456 78 90" aria-describedby="basic-addon2" required>
                <button class="p-3 w-100 rounded-0">Оставить заявку</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        message: '',
      }
    }
  }
</script>



