<template>
  <div class="card p-3">
    <div class="card__header pt-5 p-3 text-center">
      <h4>{{cardData.header}}</h4>
    </div>
    <div class="card__body">
      <p v-for="item in cardData.list"><i class="fa fa-circle mr-3"></i> {{item}}</p>

      <a class="btn btn-main price position-absolute text-uppercase shadow-none" :href="cardData.link">{{cardData.price}}</a>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      cardData: {
        header: '',
        list: [],
        price: '',
        link: '',
        required: true
      }
    }
  }
</script>

<style scoped>

</style>


