<template>
  <div>
    <promo></promo>
    <div class="container">
      <div class="block text-center">
        <h2 class="mb-5">Почему Мы?</h2>
        <p>
          Большой опыт в создании веб-приложений, самые низкие цены. Мы создатели обучающего IT  портала BulveyzCasts, портала для управления структурой колледжей Wingeter, облачных хранилищ и других крупных проектов. Почему так дешево? Потому что мы живём в 21 веке, в наше время у каждого предприятия должен быть свой собственный сайт, но из-за цен у некоторых нет возможности тратиться на создание веб-приложений, мы решили убрать “флешмоб высоких цен”  на сайты. Мы выполним сайт с любым функционалом и любого уровня сложности по самым адекватным ценам.
        </p>
      </div>

      <div class="block">
        <div class="d-flex justify-content-around flex-wrap mb-5">
          <card :card-data="card1"></card>
          <card :card-data="card2"></card>
          <card :card-data="card3"></card>
        </div>
        <div class="text-center">
          <p class="mb-5">Если вы хотите добавть любой ваш функционал или не знаете с чего и как начать, свяжитесь с нами 24/7</p>
          <router-link to="/services" class="btn btn-main">Полный список услуг</router-link>
        </div>
      </div>
    </div>
    <div class="text-center position-relative">
      <div class="overlay"></div>
    <h2>Что мы используем</h2>
      <img src="../assets/images/group.svg"  class="img-fluid w-100" alt="">
    </div>
    <main-form></main-form>
  </div>
</template>

<script>
  import card from '../components/Card'
  import promo from '../components/Header'
  import MainForm from '../components/MainForm'
  export default {
    components: {
      card,
      promo,
      MainForm
    },
    data() {
      return {
        card1: {
          header: 'Визитка',
          list: ['Индивидульный дизайн', 'Счетчик просмотров'],
          link: '#form',
          price: 'от 5.000 тг'
        },
        card2: {
          header: 'Лендинг',
          list: ['Индивидульный дизайн', 'Админ панель'],
          link: '#form',
          price: 'от 10.000 тг'
        },
        card3: {
          header: 'Магазин',
          list: ['Индивидульный дизайн', 'Админ панель', 'Техническая поддержка'],
          link: '#form',
          price: 'от 30.000 тг'
        }
      }
    }
  }
</script>




